.bar-timer-bar{
    -webkit-animation-name: 'barTimerAnimation';
            animation-name: 'barTimerAnimation';
/*    animation-iteration-count: infinite;*/
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    background-color: var(--primary-color);
    border-radius: var(--border-radius);
    height: 5px;
}

.bar-timer-field{
    background-color: var(--med-grey);
    border-radius: var(--border-radius);
    position: relative;
    width: 100wv;
}

@-webkit-keyframes barTimerAnimation {
    0% {
        width: 0%;
    }

    100% {
        width: 100%;
    }
}

@keyframes barTimerAnimation {
    0% {
        width: 0%;
    }

    100% {
        width: 100%;
    }
}




